<template>
    <div class="alert text-center" :class="classAlert" role="alert" v-if="message !== ''">
        {{ message }}
    </div>
</template>


<script setup>

import { reactive, watchEffect } from "vue";

const props = defineProps({
    message: {
        type: String,
        default: ""
    },

    isSuccess: {
        type: Boolean,
        default: true
    }
});


const classAlert = reactive({
    "alert-danger": !props.isSuccess,
    "alert-success": props.isSuccess,
});

watchEffect(() => {
    const success = props.isSuccess;
    classAlert["alert-danger"] = !success;
    classAlert["alert-success"] = success;
});

</script>