<template>
    <div class="">

        <div class="container">
            <div class="row justify-content-center align-items-center vh-100">
                <div class="col-8 col-md-6 col-lg-5">
                    <div class="login-wrap bg-white shadow p-4 p-md-5">

                        <AlertComponent :message="alertMsg" :is-success="isSuccessAlert"></AlertComponent>

                        <div class="icon d-flex align-items-center justify-content-center pb-3">
                            <svg xmlns="http://www.w3.org/2000/svg" width="3em" height="3em" fill="currentColor"
                                class="bi bi-person-fill-lock" viewBox="0 0 16 16">
                                <path
                                    d="M11 5a3 3 0 1 1-6 0 3 3 0 0 1 6 0Zm-9 8c0 1 1 1 1 1h5v-1a1.9 1.9 0 0 1 .01-.2 4.49 4.49 0 0 1 1.534-3.693C9.077 9.038 8.564 9 8 9c-5 0-6 3-6 4Zm7 0a1 1 0 0 1 1-1v-1a2 2 0 1 1 4 0v1a1 1 0 0 1 1 1v2a1 1 0 0 1-1 1h-4a1 1 0 0 1-1-1v-2Zm3-3a1 1 0 0 0-1 1v1h2v-1a1 1 0 0 0-1-1Z" />
                            </svg>
                        </div>
                        <h3 class="text-center lead mb-4">Login</h3>

                        <form @submit.prevent="login">
                            <div class="form-group mb-3">
                                <input type="text" class="form-control" placeholder="Username" v-model="username" required>
                            </div>
                            <div class="form-group d-flex mb-3">
                                <input type="password" class="form-control" placeholder="Password" v-model="password"
                                    required>
                            </div>

                            <div class="form-group mb-3 text-center">
                                <button type="submit" class="btn btn-primary p-2 px-5" :disabled="showLoading">
                                    <span class="spinner-border spinner-border-sm" role="status" aria-hidden="true"
                                        v-if="showLoading"></span>
                                    Login
                                </button>
                            </div>
                        </form>

                    </div>
                </div>
            </div>
        </div>


    </div>
</template>



<script setup>

import { ref, onMounted } from "vue";
import AlertComponent from "@/components/app-ui/AlertComponent"

import { useAuthStore } from "@/stores/auth";
import { storeToRefs } from 'pinia';

const authStore = useAuthStore();

const { isError } = storeToRefs(authStore);


const username = ref("");
const password = ref("");

const alertMsg = ref("");
const isSuccessAlert = ref(false);

const showLoading = ref(false);


const login = () => {

    showLoading.value = true
    authStore.login(username.value, password.value).then(msg => {
        alertMsg.value = msg;
        isSuccessAlert.value = !isError.value;
        showLoading.value = false;
    });
}

onMounted(() => {
    username.value = "";
    password.value = "";
    alertMsg.value = "";
});

</script>